<template>
    <div class="btn">
        <button @click="toDescription(productid)">立即购买</button>
    </div>
</template>
<script>
export default {
  props: ["productid"],
  data() {
    return {};
  },
  methods: {
    toDescription(e) {
      this.$router.push({
        path: "description",
        query: {
          productid: this.productid
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.btn {
  width: 100%;
  height: 3rem;
//   border: 1px red solid;
  margin-bottom: 3rem;
  button {
    
    position: fixed;
    bottom: 0;
    background-color: #ff8227;
    border-radius: .8rem;
    height: 3rem;
    color: white;
    margin: 0.6rem 1rem 1.1rem 1.6rem;
    width: 80%;
    border: 1px solid;
    text-align: center;
    // text-decoration: none;
    // display: inline-block;
    font-size: 16px;
  }
}
</style>


